.dropdown {
  height: 35px;
  overflow: hidden;
  display: inline-block;
  width: 180px; }

select {
  font-size: 18pt;
  background: #fff;
  max-height: 400px; }

optgroup {
  max-height: 400px; }

@media screen and (max-width: 750px) {
  .dropdown {
    width: 130px; }
  .select {
    font-size: 16pt; } }



